class Prices extends React.Component {
    state = {
        character : ''
    }
    getCharDetails = (e) => {
            let c = this.props.chars.filter(char => char.name === e.target.value)
            this.setState({ character : c })
    }
    render() {
        return(
            <div>
                <select className="form-control" placeolder="Select character..." onChange={this.getCharDetails}>
                    <option default >Select</option>
                    <option value="Luke Skywalker">Luke Skywalker</option>
                    <option value="C-3PO">C-3PO</option>
                    <option value="R2-D2">R2-D2</option>
                    <option value="Darth Vader">Darth Vader</option>
                    <option value="Leia Organa">Leia Organa</option>
                    <option value="Owen Lars">Owen Lars</option>
                </select>
                <br/>
                {this.state.character ? (
                     <div className="card border-info mb-3">
                     <div className="card-header">Character Details</div>
                     <div className="card-body">
                         <h4 className="card-title">{this.state.character[0].name}</h4>
                         <p className="card-text">Some quick example text to build on the card title and make up the bulk of the card's content.</p>
                     </div>
                 </div>
                ) : null }
               
                
                
            </div>
        )
    }
};

export default Prices;